export default (sequelize, Sequelize) => {
  const ServiceSystemStudent = sequelize.define("serviceSystemStudent", {
    login_link: {
      type: Sequelize.TEXT,
    },
    booking_link: {
      type: Sequelize.TEXT,
    },
    e_learning_link: {
      type: Sequelize.TEXT,
    },
    library_link: {
      type: Sequelize.TEXT,
    },
  });
  return ServiceSystemStudent;
};
